import { Grid } from "@material-ui/core";
import React from "react";

const About = () => {
  return (
    <Grid container direction="column" alignItems="center">
      <h3>В плеєрі реалізовано</h3>
      <ul>
        <li>прослуховування треку</li>
        <li>зміна гучності </li>
        <li>перемотка по часовій шкалі</li>
        <li>переключеня наступний/попередній</li>
        <li>можливо залишати коментарі до треку</li>
        <li>загрузка нового треку(*)</li>
        <li>видалення треку(*)</li>
        <li>без авторизації доступні всі треки у базі даних!!!</li>
        <li>при авторизації доступні треки які загрузив користувач!!!</li>
      </ul>
      (*) - необхідна авторизація
    </Grid>
  );
};

export default About;
