import { firestore, storage } from "../../firebase";
import { TrackActionTypes } from "../../types/track";

export const fetchTracks = (uid) => {
  return async (dispatch) => {
    try {
      const data = [];
      if (!uid) {
        await firestore
          .collection("trackListDemo")
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              data.push({ ...doc.data(), _id: doc.id });
            });
          });
      } else {
        await firestore
          .collection("trackListDemo")
          .where("UserUID", "==", uid)
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              data.push({ ...doc.data(), _id: doc.id });
            });
          });
      }
      dispatch({ type: TrackActionTypes.FETCH_TRACKS, payload: data });
    } catch (e) {
      dispatch({
        type: TrackActionTypes.FETCH_TRACKS_ERROR,
        payload: "FETCH TRACKS ERROR",
      });
    }
  };
};

export const deleteTrack = (id) => {
  return async (dispatch) => {
    const docRef = firestore.collection("trackListDemo").doc(id);
    docRef
      .get()
      .then((doc) => {
        if (doc.exists) {
          const delAudioRef = storage.refFromURL(doc.data().audio);
          const delPictureRef = storage.refFromURL(doc.data().picture);
          delAudioRef.delete();
          delPictureRef.delete();
          docRef.delete();
          dispatch({ type: TrackActionTypes.DELETE_TRACK, payload: id });
        } else {
          console.log("No such document!");
        }
      })
      .catch((error) => {
        console.log("Error getting document:", error);
      });
  };
};
