import {
  Card,
  Grid,
  IconButton,
  Modal,
  Backdrop,
  Fade,
  Button,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import React from "react";
import styles from "../styles/TrackItem.module.scss";
import { Delete, Pause, PlayArrow } from "@material-ui/icons";
import { useHistory } from "react-router-dom";
import { useActions } from "../hooks/useActions";
import { useDispatch, useSelector } from "react-redux";
import { deleteTrack } from "../store/actions-creators/track";
import { firestore, storage } from "../firebase";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "3px solid #f50057",
    borderRadius: 10,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    textAlign: "center",
    color: "#f50057",
  },
}));

const TrackItem = ({ track }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = (e) => {
    e.stopPropagation();
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const { setActiveTrack } = useActions();
  const { active, pause, duration, currentTime } = useSelector(
    (state) => state.player
  );
  const { uid } = useSelector((state) => state.user);

  const formatDuration = (duration) =>
    `${("0" + Math.floor((duration / 60) % 60)).slice(-2)}:${(
      "0" + Math.floor(duration % 60)
    ).slice(-2)}`;

  const playTrack = (e) => {
    e.stopPropagation();
    setActiveTrack(track);
    // playTrack();
  };

  const pauseTrack = (e) => {
    e.stopPropagation();
  };

  const deleteItem = async (e) => {
    dispatch(await deleteTrack(track._id));
    handleClose();
  };
  const style =
    active?._id !== track._id ? {} : { backgroundColor: "lightgrey" };

  return (
    <Card
      style={style}
      className={styles.track}
      onClick={() => {
        history.push("/tracks/" + track._id);
      }}
    >
      <IconButton>
        {active?._id !== track._id ? (
          <PlayArrow onClick={playTrack} />
        ) : pause ? (
          <PlayArrow onClick={playTrack} />
        ) : (
          <Pause onClick={pauseTrack} />
        )}
      </IconButton>
      <img width={70} height={70} src={track.picture} alt="cover" />
      <Grid
        container
        direction="column"
        style={{ width: 200, margin: "0 20px" }}
      >
        <div>{track.name}</div>
        <div style={{ fontSize: 12, color: "gray" }}>{track.artist}</div>
      </Grid>
      {active?._id === track._id && (
        <div>
          {formatDuration(currentTime)} / {formatDuration(duration)}
        </div>
      )}
      <IconButton style={{ marginLeft: "auto" }} onClick={handleOpen}>
        <Delete />
      </IconButton>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClick={(e) => e.stopPropagation()}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            {!uid ? (
              <>
                <h2 id="transition-modal-title">Authorization required!</h2>
                <Button
                  onClick={() => history.push("/login")}
                  variant="contained"
                  color="primary"
                >
                  Login
                </Button>
              </>
            ) : (
              <>
                <h2 id="transition-modal-title">Confirm deletion...</h2>
                <Button
                  onClick={deleteItem}
                  color="secondary"
                  variant="contained"
                >
                  Delete
                </Button>
              </>
            )}
          </div>
        </Fade>
      </Modal>
    </Card>
  );
};

export default TrackItem;
